const TRY_LOGIN = 'TRY_LOGIN';
const CHECK_LOGIN = 'CHECK_LOGIN';

let initialState = {
    login: '',
    pass: '',
    isAuth: false
}

const globalReducer = (state = initialState, action) => {

    switch (action.type){
        case TRY_LOGIN:
            console.log(action.loginData)
            return {
                ...state,
                isAuth: action.loginData[0].login === 'admin' && action.loginData[0].password === 'admin' ? true : false
            }

        default:
            return state;

    }
}

export const tryLogin = (loginData) => ({
    type: TRY_LOGIN,
    loginData
})

export default globalReducer;