import React from "react";
import Timer from "./Timer";
import {connect} from "react-redux";
import {updateTimer, EndGame, drawGameField, popUpToggle} from "../../redux/cards-reducer";
import {compose} from "redux";
import {withAuthRedirect} from "../hoc/withAuthRedirect";

class TimerContainer extends React.Component {

    timeoutId = new Set();
    activateUpdate() {
        this.props.drawGameField();
        let wtf = setInterval(()=>{
            this.timeoutId.add(wtf);
            this.timeoutId.forEach(el => console.log(el));
            this.props.updateTimer()}, 1000)
    }
    changeHappen() {
        if(this.props.fixedCards.length===36) {
            //т.к я не понял как мне получить ссылку/id активного интервала - поэтому говнокод
            for(let i=0; i<1000; i++){
                clearInterval(i)
            }
            this.props.EndGame()
            console.log('THE END')
        }
    }

    forceEnd() {
        for(let i=0; i<1000; i++){
            clearInterval(i)
        }
        this.props.EndGame()
    }
    render() {
        return <Timer {...this.props} changeHappen={this.changeHappen.bind(this)} activateUpdate={this.activateUpdate.bind(this)}
        forceEnd={this.forceEnd.bind(this)}/>
    }
}


const mapStateToProps = (state) => ({
    timer: state.cardsPage.timer,
    fixedCards: state.cardsPage.fixedCardsId,
    popUp: state.cardsPage.showPopUp,
    leaders: state.cardsPage.leaders
})

export default compose(connect(mapStateToProps, {updateTimer, EndGame, drawGameField, popUpToggle}),
    withAuthRedirect
)(TimerContainer)
