import React from 'react';
import s from './Timer.module.css'


const Timer = (props) => {

    let currentLeaders = props.leaders.map(el => <div>{el}</div>)

    return <div>
        <input type="text" disabled={true} value={props.timer} onChange={props.changeHappen()} className={s.scoreBlock}></input>
        <button disabled={props.timer!=='0'} onClick={props.activateUpdate} className={s.buttonBlock}>Press me</button>
        <button onClick={props.popUpToggle} className={s.scoreboardButton}>Scoreboard</button>
        <button onClick={props.forceEnd} className={s.buttonBlock}>End Game</button>
        {props.popUp &&  <div className={s.popUp}>{currentLeaders}</div>}
    </div>
}

export default Timer;