import './App.css';
import React from 'react';
import TimerContainer from "./components/Timer/TimerContainer";
import MyCardsContainer from "./components/Cards/MyCardsContainer";
import Navbar from "./components/Navbar/Navbar";
import {Route, Switch} from "react-router-dom";
import LoginContainer from "./components/Login/LoginContainer";
import notFoundImage from "./assets/images/404.png"
import Game from "./components/XO/Game";
import XOGameContainer from "./components/XOGameContainer/CardMemoryGameContainer";

const notFoundError = () => {
    return (
        <div className="For404">
            <img src={notFoundImage}/>
        </div>
    )
}

const App = (props) => {
    return (
        <div className="App">
            <Navbar/>
            <div className='app-wrapper-content'>
                <Switch>
                    <Route exact path='/login'
                           render={() => <LoginContainer/>}/>
                    <Route exact path='/cardmemory'
                           render={() => <div><TimerContainer/><MyCardsContainer/></div>}/>
                    <Route exact path='/xo'
                           render={() => <XOGameContainer />}/>
                    <Route exact path='/' render={()=><div>Site with mini games</div>}/>
                    <Route
                           component={notFoundError}/>
                </Switch>
            </div>
        </div>
    );
}

export default App;
