import React from "react";
import MyCards from "./MyCards";
import {connect} from "react-redux";
import {
    addFixedCards,
    drawGameField,
    openOneCard,
    removeAllOpenedCards,
    removeCardFromOpened
} from "../../redux/cards-reducer";
import {compose} from "redux";
import {withAuthRedirect} from "../hoc/withAuthRedirect";


class MyCardsContainer extends React.Component {



    componentDidMount() {
       // this.props.drawGameField();
    }

    render() {
        return <MyCards {...this.props} />
    }
}


const mapStateToProps = (state) => ({
    cardsList: state.cardsPage.cardsList,
    openedCardsId: state.cardsPage.openedCardsId,
    fixedCardsId: state.cardsPage.fixedCardsId
})

export default compose(connect(mapStateToProps, {drawGameField, openOneCard, removeCardFromOpened,
removeAllOpenedCards, addFixedCards}), withAuthRedirect)(MyCardsContainer)
