import React from "react";
import s from "./MyCards.module.css"
import styled, {keyframes} from 'styled-components';
import {fadeIn} from 'react-animations';
import CardContainer from "./Card/CardContainer";

const Animate = styled.div`animation: 1s ${keyframes`${fadeIn}`}`;
const MyCards = (props) => {

    let cardsElements =
        props.cardsList.map(p => <CardContainer id={p.id} title={p.title} imageId={p.imageId} imageSrc={p.imageSrc}
                                       openedCards={props.openedCardsId} fixedCards={props.fixedCardsId}
                                       openOne={props.openOneCard} removeCardFromOpened={props.removeCardFromOpened}
                                       cardsList={props.cardsList} removeAllOpenedCards={props.removeAllOpenedCards}
                                       addFixedCards={props.addFixedCards}
        />);

    return (
        <div className={s.cardsElements}>
            <Animate>
                {cardsElements}
            </Animate>
        </div>
    )
}

export default MyCards;