import {cardsAPI} from "../API/api";

const UPDATE_TIMER = 'UPDATE-TIMER';
const SET_CARDS_LIST = 'SET-CARDS-LIST';
const ADD_OPENED_CARD = 'ADD-OPENED-CARD';
const REMOVE_CARD_FROM_OPENED = 'REMOVE-CARD-FROM-OPENED';
const REMOVE_ALL_OPENED_CARDS = 'REMOVE-ALL-OPENED-CARDS';
const ADD_FIXED_CARDS = 'ADD-FIXED-CARDS';
const CLEAR_GAME = 'CLEAR-GAME';
const POPUP_TOGGLE = 'POPUP-TOGGLE';

let initialState = {
    openedCardsId: [],
    fixedCardsId: [],
    cardsList: [],
    timer: '0',
    leaders: [],
    showPopUp: false
}

const cardsReducer = (state = initialState, action) => {


    switch (action.type){
        case UPDATE_TIMER:
            let currentTime = parseInt(state.timer);
            return {
                ...state,
                timer: (currentTime+1).toString()
            }
        case SET_CARDS_LIST:
            return {
                ...state,
                cardsList: action.newCards
            }
        case ADD_OPENED_CARD:
            return {
                ...state,
                openedCardsId: [...state.openedCardsId, action.newCard]
            }
        case REMOVE_CARD_FROM_OPENED:
            return {
                ...state,
                openedCardsId: state.openedCardsId.filter(el => el!==action.cardForRemove)
            }
        case REMOVE_ALL_OPENED_CARDS:
            let cardsListAfterRemove = state.cardsList.filter(el => !state.fixedCardsId.includes(el.id));
            return {
                ...state,
                openedCardsId: [],
                cardsList: cardsListAfterRemove
            }
        case ADD_FIXED_CARDS:
            return {
                ...state,
                fixedCardsId: [...state.fixedCardsId, ...action.newFixedCardsList]
            }
        case CLEAR_GAME:
            debugger;
            return {
                ...state,
                cardsList: [],
                fixedCardsId: [],
                leaders: [...state.leaders, state.timer],
                openedCardsId: [],
                timer: '0'
            }
        case POPUP_TOGGLE:
            return {
                ...state,
                showPopUp: !state.showPopUp
            }
        default:
            return state;

    }
}


export const updateTimer = () => ({
    type: UPDATE_TIMER
})

export const setCardsList = (newCards) => ({
    type: SET_CARDS_LIST,
    newCards
})

export const addOpenedCard = (newCard) => ({
    type: ADD_OPENED_CARD,
    newCard
})

export const removeCardFromOpened = (cardForRemove) => ({
    type: REMOVE_CARD_FROM_OPENED,
    cardForRemove
})

export const removeAllOpenedCards = () => ({
    type: REMOVE_ALL_OPENED_CARDS
})

export const addFixedCards = (newFixedCardsList) => ({
    type: ADD_FIXED_CARDS,
    newFixedCardsList
})

export const clearGame = () => ({
    type: CLEAR_GAME
})

export const popUpToggle = () => ({
    type: POPUP_TOGGLE
})

export const drawGameField = () => {

    return (dispatch) => {
        let newCards = cardsAPI.getAllCards()
        dispatch(setCardsList(newCards))
    }
}

export const openOneCard = (newCard) => {

    return (dispatch) => {
        dispatch(addOpenedCard(newCard))
    }
}

export const EndGame = () => {

    return (dispatch) => {
        dispatch(clearGame())
    }
}

export default cardsReducer;