import React from "react";
import s from "./Card.module.css";
import standart from "../../../assets/images/creative_logo.png";

const Card = (props) => {

    return (
        <div className={s.item} onClick={props.addOneCardToOpen}>
            <span>{props.openedCards.some(el => el===props.id) || props.fixedCards.some(el => el===props.id)
                ? <img src={props.imageSrc} className={s.openedLogo}/>
                : <img src={standart} className={s.standartLogo}/>}</span>
        </div>
    )
}

export default Card