import React from "react";
import Card from "./Card";



class CardContainer extends React.Component {



    componentDidMount() {
        //console.log(this.props);
        //this.props.drawGameField();
    }
    addOneCardToOpen() {
        console.log(this.props);
        console.log(`U CLICK ON ME ${this.props.id}`)
        if(!this.props.fixedCards.some(el => el === this.props.id) && !this.props.openedCards.some(el => el === this.props.id)) {
            if (this.props.openedCards.length !== 2) {
                if (this.props.openedCards.length === 1) {
                    let cardPrevious = this.props.cardsList.filter(el => el.id === this.props.openedCards[0])[0];
                    let cardCurrent = this.props.cardsList.filter(el => el.id === this.props.id)[0];
                    if (cardPrevious.imageId === cardCurrent.imageId) {
                        this.props.addFixedCards([cardPrevious.id, cardCurrent.id])
                        this.props.removeAllOpenedCards()
                    } else {
                        this.props.openOne(this.props.id)
                    }
                } else {
                    this.props.openOne(this.props.id)
                    setTimeout(() => {
                        this.props.removeCardFromOpened(this.props.id)
                    }, 3000)
                }
            }
            if (this.props.openedCards.length !== 0) {
                setTimeout(() => {
                    this.props.removeCardFromOpened(this.props.id)
                }, 3000)
            }
        }
    }

    render() {
        return <Card {...this.props} addOneCardToOpen={this.addOneCardToOpen.bind(this)} />
    }
}

export default CardContainer
